.spinner {
  display: flex;
  justify-content: center;
  align-items: center;
}

.categories-list {
  padding: 0;
  list-style: none;
  margin-top: 24px;
}
.categories-list-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 16px;
  max-width: 360px;
  border-bottom: 1px solid #ddd;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.categories-list-item.is-active {
  background-color: rgba(184, 181, 232, .2);
}
.categories-list-item  .category-title {
  font-size: 1rem;
  font-weight: 600;
  line-height: normal;
  margin: 0;
}
.category-total {
  font-size: 12px;
  margin-left: 4px;
}

.category-actions {
  margin-left: 16px;
}
.category-actions button {
  display: inline-flex;
  justify-content: center;
  border: none;
}
.create-item.ant-form-inline  {
  display: flex;
}
.create-item .ant-switch {
min-width: 85px;
}
.chart-item {
  margin: 32px 0;
}
.chart-title {
  font-size: 20px;
  font-weight: 600;
}
.categories-info-table {
  width: 100%;
  margin-top: 32px;
}
.categories-info-table table {
  width: 100%;
}
.categories-info-table td, .categories-info-table th {
  padding: 10px;
}
.categories-info-table td:last-child, .categories-info-table th:last-child {
  text-align: right;
}
.categories-info-table table tbody tr:nth-child(odd){
  background-color: rgba(184, 181, 232, .2);
}

.categories-info-table .total {
  text-align: right;
  font-size: 1rem;
  margin-top: 16px;

}

.categories-info-table button {
  margin-left: 5px;
  margin-top: 5px;
}

.budget-header {
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  color: rgba(0, 0, 0, 0.85); 
  padding: 2px 16px;
}

.budget-header .logo {
display: flex;
align-items: center;
height: 100%;
}

.budget-header .categories-info {
  align-self: center;
  font-size: 1.1rem;
}



body * {
  font-family: 'Lato', sans-serif;
}
body {
  font-family: 'Lato', sans-serif;
  color: rgba(0, 0, 0, 0.85); 
}
.container {
  max-width: 1080px;
  margin: 0 auto;
}
.ant-layout {
  background-color: transparent!important;
}
.ant-layout-header {
  background-color: rgba(184, 181, 232, .8)!important;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.11);
}
.ant-layout-content {
  padding: 24px 16px;
}
.ant-layout-sider {
  padding: 24px 0;
}
.ant-layout-content, .ant-layout-sider {
 box-shadow: 0 4px 6px 0 rgba(28, 42, 69, 0.3);
  background-color: #fff!important;
  margin: 24px 0 96px;
  border: 1px solid rgba(28, 42, 69, 0.03)

}
.create-item .ant-form-item-label {
  display: block !important;
  text-align: left !important;
  font-size: 12px !important;
  line-height: normal;
}
.create-item .ant-form-item-label label {
  font-size: 12px !important;
  line-height: normal;
}
.btn-transparent {
  background-color: transparent !important;
  border: none !important;
}
.create-category {
  padding: 0 16px !important;
}
.create-category.ant-form-inline {
  display: flex;
}
.create-category.ant-form-inline > .ant-form-item:first-child {
  flex-grow: 1;
}
.create-category.ant-form-inline > .ant-form-item:first-child .ant-form-item-control-wrapper {
  width: 100%;
}
.create-category.ant-form-inline > .ant-form-item:last-child {
  margin-right: 0;
}

